html {
  font-family: Roboto, Poppins, sans-serif;
}

body {
  margin: 0;
}

:root {
  --main-color: #00acdc;
}

* {
  box-sizing: border-box;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo-image {
  border: 0;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav-item:not(:first-child) {
  margin-left: 20px;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav-link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

